
#app {
  font-family: 'noto',serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
@import './assets/hover-min.css';
.Tabs {
  font-family: Helvetica, Arial, Sans-Serif;
  font-weight: bold;
  color: black;
}
.Top_Panel {
  background-image: url("./assets/MainBackground.jpg");
  background-size: 100% 760px;
  width: 100%;
  height: 760px;
}
.TopPanelButtons {
  margin-left:30px;
  font-size: 24px;
  color: white;
}
.SignInButton {
  margin-left: 100px;
  font-weight: bold;
}
.SignUpButton {
  margin-left: 20px;
  font-weight: bold;
}
.PageTitle {
font-family: 'droid';
font-style: italic;
color: white;
margin-left: 250px;
font-size: 72px;
}
.PageSubtitle {
font-family: 'Arial';
font-style: italic;
color: white;
margin-left: 200px;
font-size: 32px;
}
.GetStartedButton {
  margin-left:260px;
  font-size: 24px;
  color: white;
}



